.card_article_experiencia{
    width: 60%;
    margin: 0 auto;
}
.card_article_experiencia h2{
    font: 800 32px 'Poppins';
    text-align: center;
    padding-block: 20px;
}
.card_article_experiencia p {
    font: 300 16px 'Poppins';
    text-align: center;
}

.container_card_experiencia{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-block: 70px;
}

@media(max-width: 375px){
    .container_card_experiencia{
        flex-direction: column;
    }
    .card_article_experiencia{
        width: 99%;
    }
    .card_article_experiencia h2{
        font: 800 24px 'Poppins';
    }
    .card_article_experiencia p {
        font: 300 16px 'Poppins';
    }
}
@media(min-width: 376px) and (max-width: 768px){
    .container_card_experiencia{
        flex-direction: column;
    }
    .card_article_experiencia{
        width: 90%;
    }
    .card_article_experiencia h2{
        font: 800 38px 'Poppins';
    }
    .card_article_experiencia p {
        font: 300 18px 'Poppins';
    }
}
@media(min-width: 769px) and (max-width: 1024px){
    .card_article_experiencia{
        width: 78%;
    }
    .card_article_experiencia h2{
        font: 800 48px 'Poppins';
        padding: 40px 0;
    }
    .card_article_experiencia p {
        font: 300 18px 'Poppins';
    }
}
@media(min-width: 1025px) and (max-width: 1799px){
    .card_article_experiencia{
        width: 78%;
    }
    .card_article_experiencia h2{
        font: 800 52px 'Poppins';
        padding: 40px 0;
    }
    .card_article_experiencia p {
        font: 300 20px 'Poppins';
    }
}
@media(min-width: 1800px){
    .card_article_experiencia h2{
        font: 800 64px 'Poppins';
        padding: 40px 0;
    }
    .card_article_experiencia p {
        font: 300 26px 'Poppins';
    }
}