#container_footer{
    padding-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
}
.card_info_rodape{
    width: 50%;
    font: normal 18px 'Poppins', sans-serif;
    text-align: center;
}
.card_redes_sociais img{
    width: 30px;
}
.box_logo_site{
    width: 90px;
}
.card_redes_sociais img{
    margin-left: 14px;
}
.card_author{
    font: normal 14px 'Poppins', sans-serif;
    padding-block: 10px;
}
.card_author p span{
    color: #feb633;
}
@media(max-width: 375px){
    #container_footer{
        padding-top: 60px;
        gap: 50px;
    }
    .card_info_rodape{
        width: 95%;
        font: normal 14px 'Poppins', sans-serif;;
    }
    .card_author{
        font: normal 14px 'Poppins', sans-serif;
        padding-block: 10px;
    }
    .box_logo_site{
        width: 60px;
    }
}
@media(min-width: 376px) and (max-width: 768px){
    #container_footer{
        padding-top: 60px;
        gap: 50px;
    }
    .card_info_rodape{
        width: 75%;
        font: normal 16px 'Poppins', sans-serif;;
    }
    .card_author{
        font: normal 14px 'Poppins', sans-serif;
        padding-block: 10px;
    }
}
@media(min-width: 769px) and (max-width: 1024px){
    #container_footer{
        padding-top: 60px;
        gap: 50px;
    }
    .card_info_rodape{
        width: 600px;
        font: normal 20px 'Poppins', sans-serif;;
    }
    .card_author{
        font: normal 14px 'Poppins', sans-serif;
        padding-block: 10px;
    }
}
@media(min-width: 1025px) and (max-width: 1799px){
    #container_footer{
        padding-top: 60px;
        gap: 50px;
    }
    .card_info_rodape{
        width: 700px;
        font: normal 22px 'Poppins', sans-serif;;
    }
    .card_redes_sociais img{
        width: 60px;
    }
    .card_author{
        font: normal 16px 'Poppins', sans-serif;
        padding-block: 10px;
    }
    .box_logo_site{
        width: 100px;
    }
}
@media(min-width: 1800px){
    #container_footer{
        padding-top: 60px;
        gap: 40px;
    }
    .card_info_rodape{
        width: 900px;
        font: normal 27px 'Poppins', sans-serif;;
    }
    .card_redes_sociais img{
        width: 60px;
    }
    .card_author{
        font: normal 16px 'Poppins', sans-serif;
        padding-block: 10px;
    }
    .box_logo_site{
        width: 100px;
    }
}