@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap');


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/*efeitos na troca de cor que sera usado globalmente*/
/*cor dark do header e footer*/
.dark_info{
    background: #252525;
    color: #9f9f9f;
}
/*cor light do header e footer*/
.light_info {
    background: #fff;
    color: #8a8fa3;
}

/*cor dark lista*/
.dark_lista {
    background: #393838;
    color: #9f9f9f;
}
/*cor light list*/
.light_lista{
    background-color: #f6f8fc;
    color: #32333c;
}
