.container_banner{
    display: flex;
    align-items: center;
    width: 100%;
    padding-block: 60px;
    background-color: #f6f8fc;
}
#card_banner_img{
   width: 50%;
   text-align: center;
}
.card_banner_infor{
    margin-left: 20px;
    color: #022144;
}
.card_banner_infor article p{
   
    font: 100 14px 'Poppins';
}
.card_banner_infor article h1{

    padding-block: 10px;
    font: 600 32px 'Poppins';
}
.card_banner_infor article h2{
    font: 900 30px 'Poppins';
}
@media(max-width: 375px){


    #card_banner_img{
        display: none;
    }
    .card_banner_infor{
        width: 100%;
    }
    .card_banner_infor article{
        width: 100%;
        display: flex;
        align-items: start;
        justify-content: center;
        flex-direction: column;
        min-height: 60vh;
    }
    .card_banner_infor article p{
        font: 100 12px 'Poppins';
    }
    .card_banner_infor article h1{
        padding-block: 10px;
        font: 600 20px 'Poppins';
    }
    .card_banner_infor article h2{
        font: 900 18px 'Poppins';
    }
    .card_banner_infor{
        margin-left: 50px;
    }


}
