.card_experiencia{
    width: 280px;
    height: 300px;
    padding: 10px;
    border: 0.1px solid #4c4c4c;
}
.card_experiencia div p{
    padding-block: 10px;
    font: 300 16px 'Poppins';
}
.card_experiencia div h3{
    font: 700 24px 'Poppins';
}
@media(max-width: 375px){
    .card_experiencia{
        width: 100%;
        height: auto;
        margin: 10px auto;
    }
}
@media(min-width: 376px) and (max-width: 768px){
    .card_experiencia{
        width: 90%;
        height: auto;
        margin: 10px auto;
    }
}
@media(min-width: 769px) and (max-width: 1024px){
    .card_experiencia{
        padding: 40px 10px;
        height: 330px;
    }

}
@media(min-width: 1025px) and (max-width: 1799px){
    .card_experiencia{
        width: 300px;
        height: auto;
        padding: 40px 15px;
    }
    .card_experiencia div h3{
        font: 700 26px 'Poppins';
    }
}
@media(min-width: 1800px){
    .card_experiencia{
        width: 340px;
        height: auto;
        padding: 40px 15px;
    }
    .card_experiencia div h3{
        font: 700 28px 'Poppins';
    }
    .card_experiencia div p{
        font: 300 18px 'Poppins';
    }
}